import Carousel from 'react-bootstrap/Carousel'

export default function slider() {
  return (
    <>
            <Carousel>
        <Carousel.Item interval={1000}>
          <img
            className="d-block w-100"
            src="https://www.lavanguardia.com/files/og_thumbnail/uploads/2020/12/14/5fd7240cadcfe.jpeg"
            alt="First slide"
          />
          <Carousel.Caption>
            <h3>CAFE 1  </h3>
            
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100"
            src="https://s.rfi.fr/media/display/cb870552-ed16-11eb-b7c9-005056bf30b7/w:1280/p:16x9/f81547d8c2776eb16ca5164cf698ed8e363e6142.webp"
            alt="Second slide"
          />
          <Carousel.Caption>
            <h3>CAFE 2</h3>
            
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src="data:image/jpeg;base64,"
            alt="Third slide"
          />
          <Carousel.Caption>
            <h3>CAFE 3</h3>
            
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    </>
  );
}
